body {
  background: #0e0e0e;
  position: relative;
  overflow-x: hidden;
  font: 400 16px/1.4 "Manrope", sans-serif;
  color: #f7f7f7;
}

html {
  position: relative;
  overflow-x: hidden;
}

.container {
  max-width: 1160px;
  margin: 0 auto;
  box-sizing: content-box;
  padding: 0 15px;
}

.btn__blue {
  border: none;
  padding: 15px 22px;
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 10px;
  font: 400 16px/1.4 "Manrope", sans-serif;
  color: #fff;
  border-radius: 10px;
  background: #0d6af9;
  transition: all 0.3s;
}
.btn__blue svg {
  width: 16px;
  height: 16px;
}
.btn__blue:hover {
  opacity: 0.7;
}

.nav {
  padding: 20px 0 17px;
  background: #0e0e0e;
}
.nav__overflow {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  z-index: 5;
  background: rgba(0, 0, 0, 0.6);
  pointer-events: none;
  transition: all 0.3s;
  opacity: 0;
}
.nav__overflow.active {
  opacity: 1;
  pointer-events: all;
}
.nav__hum, .nav__mob {
  display: none;
}
.nav .container {
  max-width: 1410px;
}
.nav__row {
  display: grid;
  grid-template-columns: auto auto 1fr auto auto;
  gap: 30px;
  align-items: center;
}
.nav__logo {
  width: 100%;
  max-width: 94px;
}
.nav__logo img {
  width: 100%;
}
.nav .btn__blue {
  padding: 13px;
  gap: 48px;
}
.nav .btn__blue svg {
  width: 24px;
  height: 24px;
}
.nav__search {
  position: relative;
}
.nav__search button {
  width: 50px;
  height: 50px;
  border-radius: 8px;
  display: flex;
  justify-content: center;
  align-items: center;
  background: #fff;
  transition: all 0.3s;
  position: absolute;
  top: 0;
  right: 0;
  border: none;
}
.nav__search button svg {
  width: 24px;
  height: 24px;
}
.nav__search button:hover {
  opacity: 0.7;
}
.nav__search input {
  width: 100%;
  box-sizing: border-box;
  font: 400 16px/1.4 "Manrope", sans-serif;
  color: #fff;
  border-radius: 8px;
  background: #363636;
  border: 1px solid #000;
  padding: 13px;
  padding-right: 40px;
}
.nav__search input::placeholder {
  color: #a9a7a9;
}
.nav__link {
  font: 400 16px/1.4 "Manrope", sans-serif;
  color: #fff;
  transition: all 0.3s;
}
.nav__link:hover {
  color: #0d6af9;
}
.nav__list {
  margin: 15px 0 0;
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 30px;
}
.nav__list a {
  opacity: 0.9;
  font: 400 16px/1.4 "Manrope", sans-serif;
  color: #fff;
  transition: all 0.3s;
}
.nav__list a:hover {
  color: #0d6af9;
}
.nav__drop {
  position: absolute;
  left: 0;
  width: 100%;
  top: 85px;
  display: grid;
  grid-template-columns: 306px 1fr;
  background: #404040;
  pointer-events: none;
  opacity: 0;
  transition: all 0.3s;
  z-index: 50;
}
.nav__drop.active {
  opacity: 1;
  pointer-events: all;
}
.nav__drop_list {
  background: #1c1c1c;
  padding: 10px;
  display: grid;
  grid-template-columns: 1fr;
  gap: 3px;
}
.nav__drop_list li {
  border-radius: 4px;
  padding: 5px 10px;
  display: grid;
  grid-template-columns: 18px 1fr;
  gap: 10px;
  align-items: center;
  font: 500 13px/1.4 "Manrope", sans-serif;
  color: #fff;
  transition: all 0.3s;
  cursor: pointer;
}
.nav__drop_list li.active, .nav__drop_list li:hover {
  background: #363636;
}
.nav__drop_list svg,
.nav__drop_list img {
  width: 18px;
  height: 18px;
  object-fit: contain;
  display: block;
}
.nav__drop_block {
  padding: 20px;
  display: none;
}
.nav__drop_block p {
  font: 500 24px/1 "Hauora", sans-serif;
  letter-spacing: -0.04em;
  color: #fff;
}
.nav__drop_block ul {
  grid-template-columns: auto auto;
  gap: 80px;
  row-gap: 10px;
  display: grid;
  justify-content: start;
  margin: 20px 0 0;
}
.nav__drop_block ul a {
  font: 400 16px/1.4 "Manrope", sans-serif;
  color: #fff;
  transition: all 0.3s;
}
.nav__drop_block ul a:hover {
  color: #0d6af9;
}
.nav__drop_block.active {
  display: block;
}

.header {
  min-height: 646px;
  position: relative;
  display: flex;
  flex-direction: column;
}
.header__bg {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
  z-index: -1;
}
.header h1 {
  font: 500 75px/1 "Hauora", sans-serif;
  letter-spacing: -0.04em;
  color: #f0f0f0;
}
.header h1 span {
  background: linear-gradient(49deg, #005ff9 0%, #97dbfc 100%);
  background-clip: text;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}
.header .container {
  margin-top: auto;
}
.header__info {
  padding: 13px 0 40px;
  display: grid;
  grid-template-columns: 1fr auto;
  gap: 40px;
  align-items: end;
  position: relative;
  z-index: 2;
  row-gap: 0;
}
.header__info::before {
  content: "";
  z-index: -2;
  background: linear-gradient(180deg, rgba(14, 14, 14, 0) 0%, #0e0e0e 100%);
  position: absolute;
  top: 0;
  left: 50%;
  height: 100%;
  width: 100vw;
  transform: translateX(-50%);
}
.header__text {
  margin: 10px 0 0;
}
.header .btn__blue {
  grid-column: 2/3;
  grid-row: 1/3;
}

.title {
  font: 400 16px/1.4 "Manrope", sans-serif;
  color: #fff;
  opacity: 0.7;
}
.title-main {
  font: 500 40px/1 "Hauora", sans-serif;
  letter-spacing: -0.04em;
  color: #fff;
}
.title-main span {
  letter-spacing: -0.04em;
  text-align: center;
  background: linear-gradient(49deg, #005ff9 0%, #97dbfc 100%);
  background-clip: text;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

.brand {
  padding: 100px 0 50px;
  background: url("../img/brand_bg.svg") center no-repeat;
  background-size: cover;
  position: relative;
  z-index: 2;
}
.brand::before {
  content: "";
  display: block;
  position: absolute;
  bottom: 0;
  left: 50%;
  transform: translateX(-50%);
  width: 1160px;
  height: 262px;
  border-radius: 100%;
  background: #0064ff;
  filter: blur(132.5px);
  z-index: -1;
  opacity: 0.2;
}
.brand__slider {
  margin: 30px 0 0;
  overflow: visible !important;
}
.brand__title {
  display: flex;
  justify-content: space-between;
}
.brand__more {
  font: 400 16px/1.4 "Manrope", sans-serif;
  text-align: right;
  color: #fff;
  opacity: 0.7;
  transition: all 0.3s;
}
.brand__more:hover {
  opacity: 1;
}
.brand .swiper-wrapper {
  z-index: 3;
  overflow: visible;
}
.brand .swiper-slide {
  border: 1px solid rgba(2, 97, 249, 0.1);
  border-radius: 10px;
  background: linear-gradient(135deg, #093780 0%, #0e141d 49%, #0c1f3d 100%);
  padding: 40px 20px;
}
.brand .swiper-slide img {
  height: 85px;
  max-width: 240px;
  width: 100%;
  display: block;
  margin: 0 auto;
  object-fit: contain;
}

.purpose {
  padding: 50px 0 100px;
  position: relative;
  overflow: hidden;
  z-index: 2;
}
.purpose::before {
  content: "";
  display: block;
  width: 100vw;
  height: 250px;
  position: absolute;
  left: 0;
  bottom: 0;
  background: url("../img/purpose_bg.png") top no-repeat;
  background-size: cover;
  z-index: -1;
}
.purpose h2 {
  margin: 30px 0 0;
}
.purpose__subtitle {
  margin: 10px 0 0;
  font: 400 16px/1.4 "Manrope", sans-serif;
  color: #f7f7f7;
}
.purpose__img {
  width: 100%;
  margin: 30px 0 0;
  border-radius: 10px;
}
.purpose__har {
  margin: 30px auto 0;
  display: grid;
  grid-template-columns: 1fr 1fr;
  max-width: 600px;
  position: relative;
}
.purpose__har p {
  font: 600 80px/1 "Hauora", sans-serif;
  letter-spacing: -0.04em;
  text-align: center;
  background: linear-gradient(49deg, #005ff9 0%, #97dbfc 100%);
  background-clip: text;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}
.purpose__har p + p {
  margin: 10px 0 0;
  font: 600 16px/1.4 "Manrope", sans-serif;
  color: #fff;
  -webkit-text-fill-color: #fff;
}

.delivery {
  position: relative;
  background: url("../img/delivery_bg.svg") center no-repeat;
  background-size: cover;
  padding: 50px 0 100px;
}
.delivery h2 {
  margin: 10px 0 0;
}
.delivery__wrapper {
  margin: 30px 0 0;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr;
  gap: 5px;
}
.delivery__item {
  border-radius: 10px;
  background: #1a2333;
  padding: 20px;
  display: flex;
  flex-direction: column;
}
.delivery__item p {
  font: 400 16px/1.4 "Manrope", sans-serif;
  color: #fff;
  opacity: 0.7;
}
.delivery__item p + p {
  font: 500 20px/1.2 "Hauora", sans-serif;
  letter-spacing: -0.04em;
  opacity: 1;
  margin: 10px 0 auto;
}
.delivery__item img {
  width: 100%;
  height: 181px;
  object-fit: cover;
  border-radius: 10px;
  margin: 20px 0 0;
}
.delivery .btn__blue {
  margin: 5px 0 0;
  width: 100%;
}

.choose {
  background: url("../img/choose_bg.png") center no-repeat;
  background-size: cover;
  position: relative;
  overflow: hidden;
  z-index: 2;
  padding: 100px 0;
}
.choose::before {
  content: "";
  display: block;
  position: absolute;
  border-radius: 100%;
  background: #0d6af9;
  filter: blur(60px);
  top: -114px;
  left: 0;
  width: 100vw;
  height: 227px;
  opacity: 0.7;
  z-index: -1;
}
.choose::after {
  content: "";
  display: block;
  position: absolute;
  border-radius: 100%;
  background: #0d6af9;
  filter: blur(60px);
  bottom: -60px;
  left: 50%;
  transform: translateX(-50%);
  width: 2000px;
  height: 144px;
  opacity: 0.7;
  z-index: -1;
}
.choose__title {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 0;
}
.choose__title p {
  max-width: 513px;
  margin: 0 0 0 auto;
  font: 400 16px/1.4 "Manrope", sans-serif;
  color: #f7f7f7;
}
.choose__wrapper {
  margin: 50px 0 0;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr;
}
.choose__item {
  min-height: 235px;
  backdrop-filter: blur(10px);
  background: rgba(113, 164, 254, 0.1);
  padding: 30px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.choose__item p {
  font: 600 20px/1.4 "Manrope", sans-serif;
  color: #fff;
}
.choose__item p + p {
  font: 400 14px/1.4 "Manrope", sans-serif;
  opacity: 0.9;
}
.choose__item:nth-child(3) {
  grid-column: 4/5;
}
.choose__item:nth-child(6) {
  grid-column: 2/3;
}
.choose__item:nth-child(7) {
  grid-column: 4/5;
}
.choose__wht {
  grid-column: 3/4;
  background: #edf3fe;
  padding: 20px;
  display: flex;
  flex-direction: column;
}
.choose__wht p {
  font: 600 20px/1.4 "Manrope", sans-serif;
  color: #000;
}
.choose__wht p + p {
  margin: 5px 0 auto;
  font: 400 14px/1.4 "Manrope", sans-serif;
  opacity: 0.9;
}
.choose__wht .btn__blue {
  width: 100%;
  margin: 30px 0 0;
}

.mediacenter {
  padding: 50px 0 90px;
  background: url("../img/mediacenter_bg.svg") center no-repeat;
  background-size: contain;
}
.mediacenter__title {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.mediacenter__link {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 13px;
}
.mediacenter__link a {
  display: block;
  padding: 17px 32px;
  font: 700 12px/1.05 "Manrope", sans-serif;
  letter-spacing: 0.05em;
  text-transform: uppercase;
  color: #fff;
  transition: all 0.3s;
  border: 1px solid #fff;
  border-radius: 30px;
  background: transparent;
}
.mediacenter__link a:hover {
  color: #0d6af9;
  background: #fff;
}
.mediacenter__tab {
  display: flex;
  gap: 10px;
  align-items: center;
  justify-content: start;
  margin: 50px 0 0;
}
.mediacenter__tab a {
  font: 400 16px/1.4 "Manrope", sans-serif;
  color: #fff;
  opacity: 0.5;
  transition: all 0.3s;
}
.mediacenter__tab a:hover, .mediacenter__tab a.active {
  opacity: 1;
}
.mediacenter__wrapper {
  margin: 10px 0 0;
  grid-template-columns: 1fr 1fr 1fr;
  gap: 26px;
  display: none;
}
.mediacenter__wrapper.active {
  display: grid;
}
.mediacenter__item {
  position: relative;
}
.mediacenter__item img {
  width: 100%;
  height: 212px;
  border-radius: 15px;
  object-fit: cover;
}
.mediacenter__item_date {
  position: absolute;
  top: 10px;
  left: 10px;
  font: 700 12px/1.05 "Manrope", sans-serif;
  letter-spacing: 0.05em;
  color: #fff;
  padding: 5px 10px;
  border: 1px solid #fff;
  border-radius: 300px;
}
.mediacenter__item_text {
  margin: 15px 0 0;
  font: 500 16px/1.4 "Manrope", sans-serif;
  color: #fff;
}
.mediacenter__item_btn {
  margin: 20px 0 0;
  border: 1px solid #fff;
  border-radius: 9px;
  padding: 15px 30px;
  font: 500 16px/1.05 "Manrope", sans-serif;
  letter-spacing: 0.05em;
  color: #fff;
  transition: all 0.3s;
  display: block;
  width: max-content;
}
.mediacenter__item_btn:hover {
  color: #0d6af9;
  background: #fff;
}

.footer {
  position: relative;
  overflow: hidden;
  padding: 60px 0 40px;
}
.footer h2 {
  font: 500 60px/1 "Hauora", sans-serif;
  letter-spacing: -0.04em;
  color: #fff;
}
.footer::before {
  content: "";
  display: block;
  position: absolute;
  border-radius: 100%;
  background: #0d6af9;
  filter: blur(60px);
  top: -114px;
  left: 0;
  width: 100vw;
  height: 227px;
  opacity: 0.7;
  z-index: -1;
}
.footer iframe {
  margin: 50px 0 0;
  border-radius: 15px;
}
.footer__wrapper {
  margin: 20px 0 0;
  display: grid;
  grid-template-columns: 2fr 1fr;
  gap: 30px;
  align-items: start;
}
.footer__har {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 30px;
}
.footer__har div {
  max-width: 400px;
}
.footer__har p {
  font: 400 16px/1.4 "Manrope", sans-serif;
  text-transform: uppercase;
  color: #fff;
  opacity: 0.6;
}
.footer__har p + p {
  margin: 10px 0 0;
  opacity: 1;
  text-transform: none;
}
.footer__har a {
  display: block;
  font: 400 20px/1.4 "Manrope", sans-serif;
  color: #fff;
  margin: 10px 0 0;
  transition: all 0.3s;
}
.footer__har a:hover {
  color: #0d6af9;
}
.footer__har .soc div {
  display: flex;
  justify-content: start;
  align-items: center;
  margin: 10px 0 0;
  gap: 7px;
}
.footer__har .soc a {
  margin: 0;
  width: 55px;
  height: 55px;
  border-radius: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  background: #f8f8f8;
  transition: all 0.3s;
}
.footer__har .soc a:hover {
  opacity: 0.7;
}
.footer__list {
  display: grid;
  gap: 15px;
}
.footer__list a {
  font: 400 16px/1.4 "Manrope", sans-serif;
  color: #fff;
  transition: all 0.3s;
}
.footer__list a:hover {
  color: #0d6af9;
}
.footer__list li:last-child {
  display: none;
}
.footer__row {
  display: flex;
  align-items: end;
  justify-content: space-between;
  margin: 35px 0 0;
  padding: 13px 0 0;
  border-top: 2px solid rgba(13, 107, 249, 0.3294117647);
}
.footer__row p,
.footer__row a {
  font: 400 16px/1.4 "Manrope", sans-serif;
  color: #fff;
  transition: all 0.3s;
}
.footer__row a:hover {
  color: #0d6af9;
}
.footer__row p {
  margin: 12px 0 0;
}
.footer__row .btn__blue {
  align-self: start;
}
.footer__row img {
  width: 144px;
}

@media (max-width: 1200px) {
  .nav__row {
    gap: 15px;
  }
  .header .btn__blue {
    grid-row: auto;
  }
  .header h1 {
    grid-column: 1/3;
  }
  .header__info {
    gap: 10px;
  }
  .header__text {
    margin: 0;
  }
  .brand .swiper-slide {
    width: 250px;
  }
  .brand .swiper-slide img {
    height: 85px;
  }
  .choose__wrapper {
    grid-template-columns: 1fr 1fr 1fr;
  }
  .choose__item:nth-child(3), .choose__item:nth-child(7) {
    grid-column: auto;
  }
  .choose__item:nth-child(6) {
    grid-column: 2/3;
  }
  .choose__wht {
    grid-column: 3/4;
  }
}
@media (max-width: 960px) {
  .nav {
    padding: 20px 0;
  }
  .nav__row {
    gap: 10px;
    grid-template-columns: auto auto 1fr auto;
  }
  .nav__link {
    display: none;
  }
  .nav__list {
    display: none;
  }
  .nav__hum {
    display: block;
    width: 50px;
    height: 50px;
    border-radius: 8px;
    background: #fff;
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;
  }
  .nav .btn__blue {
    gap: 14px;
  }
  .nav__mob {
    display: block;
    position: absolute;
    top: 0;
    right: 0;
    width: 328px;
    height: 100vh;
    background: #0e0e0e;
    z-index: 50;
    padding: 30px;
    display: flex;
    flex-direction: column;
    pointer-events: none;
    opacity: 0;
    transition: all 0.3s;
  }
  .nav__mob.active {
    opacity: 1;
    pointer-events: all;
  }
  .nav__mob_search {
    display: none;
  }
  .nav__mob_list {
    display: grid;
    grid-template-columns: 1fr;
    gap: 10px;
    margin: 0 0 auto;
  }
  .nav__mob_link {
    margin: 20px 0 0;
    display: grid;
    grid-template-columns: 1fr;
    gap: 10px;
  }
  .nav__mob_link a {
    font: 500 20px/1.4 "Hauora", sans-serif;
    letter-spacing: -0.04em;
    color: #fff;
    padding: 13px;
    border: 1px solid #fff;
    border-radius: 33px;
    display: block;
    text-align: center;
  }
  .nav__mob_link a + a {
    color: #0e0e0e;
    background: #fff;
  }
  .nav__mob_soc {
    margin: 20px 0 0;
    display: flex;
    justify-content: start;
    align-items: center;
    gap: 5px;
  }
  .nav__mob_soc a {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 34px;
    height: 34px;
    border-radius: 100%;
    background: #f8f8f8;
  }
  .header__info {
    display: block;
    padding: 45px 0 15px;
  }
  .header h1 {
    font-size: 51px;
  }
  .header__text {
    margin: 10px 0 0;
    font-size: 16px;
  }
  .header .btn__blue {
    margin: 10px 0 0;
  }
  .brend {
    padding: 60px 0 50px;
  }
  .title-main {
    font-size: 35px;
  }
  .purpose {
    padding: 20px 0 40px;
  }
  .purpose h2 {
    margin: 10px 0 0;
  }
  .purpose__img {
    margin: 20px 0 0;
  }
  .delivery {
    padding: 50px 0 40px;
  }
  .delivery__wrapper {
    margin: 20px 0 0;
    grid-template-columns: 1fr 1fr;
  }
  .choose {
    padding: 70px 0 40px;
  }
  .choose__title {
    gap: 12px;
    grid-template-columns: 1fr;
  }
